export const polygonPoints = [
  30, 30,
  50, 25,
  50, 190,
  150, 200,
  150, 140,
  180, 130,
  180, 230,
  30, 230,
]

export const random = (
  minNumPoints = 100, maxNumPoints = 200,
  width = 300, height = 300,
  offsetX = 30, offsetY = 30,
) => {
  const pts: number[] = []
  const numpts = Math.floor(Math.random() * (maxNumPoints - minNumPoints + 1)) + minNumPoints
  for (let i = 0; i < numpts; i += 1) {
    const genx = Math.floor(Math.random() * width) + offsetX
    const geny = Math.floor(Math.random() * height) + offsetY
    pts.push(genx, geny)
  }
  return pts
}

const pseudoNormal = () => {
  const v = Math.random() + Math.random() + Math.random() + Math.random() + Math.random() + Math.random();
  return Math.min(0.5 * (v - 3) / 3, 1);
}

export const gaussian = (count: number) => {
  const points: number[] = []
  for (let i = 0; i < count; i += 1) {
    points.push(pseudoNormal() * 1e3 + 100, pseudoNormal() * 1e3 + 100)
  }
  return points
}
