import { IPoint2D } from '../util'
import { DelaunayTriangulation } from '../triangulation'
import { isPointInTriangle } from '../boolean-operations'

const splitSign = ':'
const VoronoiDiagram = (
  points: number[],
) => {
  const triangles = DelaunayTriangulation(points)

  const edges: IPoint2D[][] = []
  const edgeExcenterMap: Map<string, number[]> = new Map()
  triangles.forEach((triangle, index) => {
    const edge1 = [triangle.p0, triangle.p1]
      .sort((a, b) => (a[0] - b[0] > 0 ? 1 : -1)).flat().join(splitSign)
    const edge2 = [triangle.p1, triangle.p2]
      .sort((a, b) => (a[0] - b[0] > 0 ? 1 : -1)).flat().join(splitSign)
    const edge3 = [triangle.p0, triangle.p2]
      .sort((a, b) => (a[0] - b[0] > 0 ? 1 : -1)).flat().join(splitSign)
    if (edgeExcenterMap.has(edge1)) {
      const edge = edgeExcenterMap.get(edge1)
      edge!.push(index)
    } else {
      edgeExcenterMap.set(edge1, [index])
    }
    if (edgeExcenterMap.has(edge2)) {
      const edge = edgeExcenterMap.get(edge2)
      edge!.push(index)
    } else {
      edgeExcenterMap.set(edge2, [index])
    }
    if (edgeExcenterMap.has(edge3)) {
      const edge = edgeExcenterMap.get(edge3)
      edge!.push(index)
    } else {
      edgeExcenterMap.set(edge3, [index])
    }
  })

  edgeExcenterMap.forEach((indice, edge) => {
    if (indice.length === 2) {
      edges.push(indice.map((index) => triangles[index].circumCircle!.center))
    } else if (indice.length === 1) {
      const [edge0x, edge0y, edge1x, edge1y] = edge.split(splitSign)
      const tri = triangles[indice[0]]
      const excenter = tri.circumCircle!.center
      const isInside = isPointInTriangle(excenter, tri.p0, tri.p1, tri.p2)
      const edgeCenter = [
        (Number(edge0x) + Number(edge1x)) / 2,
        (Number(edge0y) + Number(edge1y)) / 2,
      ]
      const p1 = excenter
      let p2
      if (isInside) {
        p2 = [
          excenter[0] + (edgeCenter[0] - excenter[0]) * 1000,
          excenter[1] + (edgeCenter[1] - excenter[1]) * 1000,
        ]
      } else {
        p2 = [
          excenter[0] + (excenter[0] - edgeCenter[0]) * 1000,
          excenter[1] + (excenter[1] - edgeCenter[1]) * 1000,
        ]
      }
      edges.push([
        p1,
        p2,
      ])
    }
  })
  return edges
}

export default VoronoiDiagram
