export type HermiteSplineConfig = {
  derivatives: number[]
}

export const DefaultHermiteConfig = (length): HermiteSplineConfig => ({
  derivatives: new Array(length).fill(0),
})

export type CardinalSplineConfig = {
  vp0: [number, number], // start virtual point
  vpn: [number, number], // end virtual point
  tension: number
}

export const DefaultCardinalConfig = (start: number[], end: number[]): CardinalSplineConfig => ({
  vp0: [start[0] + 0.01, start[1] + 0.01],
  vpn: [end[0] + 0.01, end[1] + 0.01],
  tension: 0.1,
})
